import { useState } from "react";

export default function AddTodo({ handleAdd }) {
  const [text, setText] = useState("");
  return (
    <div>
      <input
        type="text"
        placeholder="Add todo"
        onChange={(e) => setText(e.target.value)}
      />{" "}
      <button onClick={() => handleAdd(text)}>ADD TODO</button>
    </div>
  );
}
