import { useEffect, useState } from "react";
import AddTodo from "./AddTodo";
import Pagination from "./Pagination";
import Sorting from "./Sorting";
import TodoList from "./TodoList";

export default function Todo() {
  const [todos, setTodos] = useState([]);
  const [page, setPage] = useState(1);
  const [loading, setLoading] = useState(false);

  let getData = async (page, orderBy, sortBy) => {
    setLoading(true);
    let res = await fetch(
      ` https://my-newap.herokuapp.com/posts?_page=${page}&_sort=${sortBy}&_order=${orderBy}`
    );
    let res_data = await res.json();
    setLoading(false);
    setTodos(res_data);
  };

  let postData = async (text) => {
    await fetch(` https://my-newap.herokuapp.com/posts`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        title: text,
        status: false
      })
    });
    await getData();
  };

  let patchData = async (id, status) => {
    await fetch(` https://my-newap.herokuapp.com/posts/${id}`, {
      method: "PATCH",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        status: !status
      })
    });
    await getData();
  };

  let deletData = async (id) => {
    await fetch(` https://my-newap.herokuapp.com/posts/${id}`, {
      method: "DELETE",
      headers: { "Content-Type": "application/json" }
    });
    await getData();
  };

  let handleAdd = (text) => {
    postData(text);
  };

  let handleToggle = (id, status) => {
    patchData(id, status);
  };

  let handleDelete = (id) => {
    deletData(id);
  };

  let handleAsc = (page) => {
    getData(page, "ASC", "title");
  };

  let handleDesc = (page) => {
    getData(page, "DESC", "title");
  };

  useEffect(() => {
    getData(page);
  }, [page]);

  return (
    <div>
      <h1>Todo App</h1>
      <AddTodo handleAdd={handleAdd} />
      <br />
      <Sorting handleDesc={handleDesc} handleAsc={handleAsc} />
      <br />
      {loading && "Loading.."}
      <br />
      {/* <button>{MakeAscending?}</button> */}
      <br />
      {todos.map((el) => (
        <TodoList
          key={el.id}
          id={el.id}
          title={el.title}
          status={el.status}
          handleToggle={handleToggle}
          handleDelete={handleDelete}
        />
      ))}
      <br />
      <h1> {page}</h1>
      <button disabled={page === 1} onClick={() => setPage(page - 1)}>
        PREV
      </button>
      <Pagination current={page} total={10} onChange={setPage} />

      <button disabled={page === 10} onClick={() => setPage(page + 1)}>
        NEXT
      </button>
    </div>
  );
}
