export default function TodoList({
  handleDelete,
  handleToggle,
  id,
  title,
  status
}) {
  return (
    <div>
      <b>{title}</b> {status ? "Done" : "Not Done"}
      <button onClick={() => handleToggle(id, status)}>Toggle</button>
      <button onClick={() => handleDelete(id)}>Delete</button>
    </div>
  );
}
